import axios from 'axios';
import { getCookie, removeCookie, setCookie } from './Cookie';

    const refreshAPI = axios.create({
      baseURL: "http://172.16.1.112:8080/",
      headers: {
        "Content-Type": "application/json",
        "Access" : `${localStorage.getItem('access')}`,
        "Refresh": `${getCookie('refresh')}`
      } 
    });

    refreshAPI.interceptors.response.use(
      //   
      response => {
        console.log('Starting Request', JSON.stringify(response))
        return response;
      },
      async error => {
        const originalConfig = error.config; //    
        const response = error.response; //  
        // console.log(originalConfig);
        if (response) { //   
          const msg = response.data.code; // error msg from backend
          const status = response.status; //    
          console.log(status);
          // access_token 

            if(msg == '1') {
               console.log("  ");
               
              try {
                const res = await axios.post(
                  'http://172.16.1.112:8080/reissue',{},
                  {headers: {
                    Authorization: `${localStorage.getItem('access')}`,
                    Refresh: `${getCookie('refresh')}`,
                  },
                    withCredentials: true
                  },                  
                )
                .then((res)=>{
                  //   
                  localStorage.setItem("access", res.data.access);

                  //           (header     )
                  originalConfig.headers["Access"]=res.data.access;
      
                  console.log("New access token obtained.");
                  
                  window.location.reload();
                  //   
                  return refreshAPI(originalConfig);
                })
                .catch(()=>{
                    localStorage.clear();
                    removeCookie('refresh');
                    window.alert("    .");
                    window.location.href='/login';
                })  
              } catch (refreshError) {
                console.error('An error occurred while refreshing the token:', refreshError);
              }
            }
          } 
        //       
        return Promise.reject(error);
      },
    );
    
    export default refreshAPI;